.page {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  flex-shrink: 4;
  max-height: 95vh;
  min-height: 95vh;
}

.main {
  display: block;
  margin: auto;
}

.contents {
  height: 80vh;
  width: 1280px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: stretch;
  flex-shrink: 4;
}

@media screen and (max-width: 1200px) {
  
  .page {
    max-height: max-content;
    padding-bottom: 48px;
  }

  .contents {
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    height: auto;
    width: auto;
  }
  
  .page {
    max-height: none;
  }

}